import React, { Component } from 'react';
import PropTypes from 'prop-types';

class Test extends Component {

    render() {
        console.log(this.props);
        const { name, onClick } = this.props;
        return (
            <div
                onClick={onClick}
            >
                {name}
            </div>
        );
    }
}

Test.propTypes = {
    name: PropTypes.string.isRequired,
}

export default Test;


