const app = (state = { count: 0, name: "" }, action) => {
    const count = state.count
    switch (action.type) {
        case 'increase':
            return { ...state, count: count + 1 }
        case 'cut':
            return { ...state, count: count - 1 }
        case 'clear':
            return { count: 0, name: "" }
        default:
            return state
    }
}

export default app;


